import React, { useState, useEffect } from "react";
import "./onOffSlider.css";
function OnOffSlider({ id, googleStorageKey }) {
    const [isChecked, setIsChecked] = useState(false);
    const [isLoaded, setIsLoaded] = useState(false);
    useEffect(() => {
        chrome.storage.sync.get([googleStorageKey], (result) => {
            setIsChecked(result[googleStorageKey] || false);
            setIsLoaded(true);
        });
    }, [googleStorageKey]);
    const handleChange = () => {
        const newValue = !isChecked;
        chrome.storage.sync.set({ [googleStorageKey]: newValue });
        setIsChecked(newValue);
    };
    if (!isLoaded) {
        return null; // or a loading indicator
    }
    return (React.createElement("div", { className: "slider-container" },
        React.createElement("input", { type: "checkbox", id: id, className: "slider-checkbox", checked: isChecked, onChange: handleChange }),
        React.createElement("label", { className: "slider-switch", htmlFor: id },
            React.createElement("span", { className: "slider-slider" }))));
}
export default OnOffSlider;
